nav{
    display: flex;
    border-bottom: 2px solid #f3f3f3;
    align-items: center;
    justify-content: space-around;
    padding: 20px;
    margin-left: 12rem;
    z-index: 999;
}

input{
    padding: 12px 20px;
    border: none;
    background: #f7f6f6;
    outline: none;
    margin-right: 20px;
    border-radius: 5px;
    position: relative;
    width: 14rem;
}

.nav-icons{
    width: 1rem;
    height: 1rem;
    margin-left: 2rem;
}

@media screen and (max-width: 768px) {
    nav {
      flex-direction: column;
      align-items: flex-start;
      margin-left: 0; 
    }
  
    input {
      width: 100%;
      margin: 0; 
      margin-bottom: 10px;
    }
  
    .nav-icons {
      margin-left: 0; 
      margin-right: 1rem;
    }
  }
  
