.recommended-flex {
  display: flex;
  flex-wrap: wrap;
  margin-left: 20rem;
  margin-right: 20px;
  justify-content: space-around;
  margin-top: 10px;
}

.recommended-title {
  margin-left: 20rem;
  margin-bottom: 20px;
  margin-top: 20px;
  font-size: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.recommended-container {
  max-width: 80%; 
  margin-left: auto;
  margin-right: auto;
}

.btns {
  padding: 10px 20px;
  margin-right: 6px;
  background: transparent;
  border: none;
  border: 0.6px solid #ccc;
  border-radius: 5px;
  color: #323232;
  cursor: pointer;
  margin: 5px;
  font-size: 14px;
}

@media screen and (max-width: 768px) {
  .recommended-flex {
    margin-left: 0; 
    flex-direction: row; 
    align-items: center; 
  }

  .recommended-title {
    margin-left: 0;
    text-align: center;
  }

  .btns {
    margin: 5px 0; 
  }
}






