.card-container {
  display: flex;
  flex-wrap: wrap;
  margin-left: 20rem; 
  margin-top: 2rem;
  z-index: -2;
}

.card {
  margin: 20px;
  border: 2px solid #ededed;
  padding: 20px;
  cursor: pointer;
  max-width: 320px;
  overflow: hidden;
  width: 280px;
}

.card-img {
  width: 230px;
  height: 250px;
  object-fit: fill;
  margin-bottom: 1rem;
}

.card-title {
  margin-bottom: 1rem;
}

.card-reviews {
  margin-bottom: 1rem;
  display: flex;
}

.rating-star {
  color: #d5ab55;
}

.total-reviews {
  font-size: 0.9rem;
  margin-left: 10px;
}

.card-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bag-icon {
  color: #535353;
}


/* Add styles for smaller screens (e.g., tablets and mobile devices) */
@media screen and (max-width: 768px) {
  .card-container {
    margin-left: 0;
    align-items: center;
    justify-content: center;
  }

  

}