.sidebar-title{
    font-size: 22px;
    font-weight: normal;
    margin-bottom: 20px;
}
.sidebar{
    width: 20%;
    position: fixed;
    height: 100%;
    max-height: 99%;
    overflow-y: auto;
    overflow-x: hidden;
    border: 2px solid #e5e5e5;
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.logo-container{
    margin-bottom: 4rem;
}
.logo-container h1{
    margin-top: 1.3rem;
}

